@import 'common';

.App {
  text-align: center; }


.App-header {
  background-color: $index-background-color;
  min-height: 55vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  font-size: calc(10px + 2vmin);
  color: $index-foreground-color; }


.App-body {
  background-color: $index-background-color;
  color: $index-foreground-color;
  height: 45vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-grow: 1;
  flex-basis: 0; }


h1 {
  font-size: 6.5vh;
  font-weight: lighter; }
